.int-grid {
	/* padding: 2em; */
	display: grid;
	grid-template-areas: 'one two three' 'four five six';
	/* 'menu footer footer footer footer footer'; */
	grid-gap: 10px;
	width: 100%;
	/* background: white; */
}

.int-grid > div > div {
	/* padding: 2em; */
	margin: 20px 10px;
	/* background-color: #1f66ad; */
	width: 350px;
	height: 300px;
	display: block;
	box-shadow: 0 0 5px #fff;
	/* margin: 0 auto; */
	transition: transform 100ms ease-out;
	border: 1px solid #fff;
	/* background: red; */

	transform-style: preserve-3d;
}
.int-grid > div > div:hover {
	/* transform: scale(1.1); */
	/* width: 400px;
  height: 350px; */
	/* transform: translateZ(-500px); */
	/* box-shadow: 0 0 30px #fff; */
	/* border: 3px solid rgba(255, 255, 0, 1); */
}
.inst-cover {
	padding: 1em;
	background: #000;
	-webkit-animation: pulse 2s infinite;
	        animation: pulse 2s infinite;
}
.inst-cover:hover {
	-webkit-animation: none;
	        animation: none;
}
@-webkit-keyframes pulse {
	25% {
		transform: scale(99%);
	}
	50% {
		transform: scale(100%);
	}
	100% {
		transform: scale(99%);
	}
}
@keyframes pulse {
	25% {
		transform: scale(99%);
	}
	50% {
		transform: scale(100%);
	}
	100% {
		transform: scale(99%);
	}
}
